.table {
  width: 400px;
  height: auto;
  margin: 30px auto;
  background-color: #fff;
  text-align: center;
}

.table th {
  font-weight: bold;
  text-align: left;
  border: none;
  padding: 10px 15px;
  background: #d8d8d8;
  font-size: 14px;
}
.table tr th:first-child {
  border-radius: 8px 0 0 8px;
}
.table tr th:last-child {
  border-radius: 0 8px 8px 0;
}
